
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import { setTimeout } from 'timers';

export default {
name:"teamM1",
data(){
return {
chartSH:false,
dateTitle1:"请选择加盟开始时间",
dateTitle2:"请选择加盟结束时间",
pickTitle:"请选择选择级别",
startTime:"",
endTime:"",
phoneVal:"",
pickVal:[],



list:[['特约服务商','高级服务商','区域总代']],
}
},
components:{
Group,
Datetime,
XInput,
PopupPicker,
Alert
},
methods:{

timeChange1(value){
this.startTime = value;
if(value){
this.dateTitle1="";
}
},
timeChange2(value){
this.endTime = value;
if(value){
this.dateTitle2="";
}
},

pickChange(val){
if(val){
this.pickTitle="";
}
},

resetBtn(){
this.startTime="";
this.dateTitle1="请选择加盟开始时间";
this.dateTitle2="请选择加盟结束时间";
this.phoneVal="";
this.pickVal=[]
let that = this;
setTimeout(function(){
that.pickTitle="请选择选择级别";
},10)
localStorage.removeItem("teamStartTime");
localStorage.removeItem("teamEndTime");
localStorage.removeItem("teamPhoneVal")
localStorage.removeItem("service_level")
},

queryBtn(){

if(this.startTime==""&&this.endTime==""&&this.phoneVal==[]&&this.pickVal==""){
this.chartSH = true;
}else{
localStorage.removeItem("teamStartTime");
localStorage.removeItem("teamEndTime");
localStorage.removeItem("teamPhoneVal")
localStorage.removeItem("service_level")
localStorage.setItem("teamStartTime",this.startTime);
localStorage.setItem("teamEndTime",this.endTime);
localStorage.setItem("teamPhoneVal",this.phoneVal)
if(this.pickVal[0]=="特约服务商"){localStorage.setItem("service_level",2)}
if(this.pickVal[0]=="高级服务商"){localStorage.setItem("service_level",3)}
if(this.pickVal[0]=="区域总代"){localStorage.setItem("service_level",4)}
this.$router.push({path:'team'});
}
}
}
}



