// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
// import Home from './components/HelloFromVux'
import axios from 'axios'
import qs from 'qs'
import router from './router'
import store from './store/index';

//Vue全局对象可用
Vue.prototype.$qs = qs

Vue.use(VueRouter)

axios.defaults.baseURL = 'http://service_api.ameimeika.com/api';

// 全局拦截
const whiteList=['/'];//不需要登录能访问的path
router.beforeEach((to, from, next) => {
    let tokenst=localStorage.getItem("tokent")+" "+localStorage.getItem("token")
    axios.defaults.headers['Authorization'] = tokenst
    //console.log('beforeEach');
    if(to.meta.title){
        document.title = to.meta.title
    }
    var userInfo = localStorage.getItem("token");
    // console.log(userInfo)
    if(whiteList.indexOf(to.path)<0){//访问了需要登录才能访问的页面
        if(userInfo){//登录过来直接进去
            next();
        }else{
            if(to.path=='/'){
                next();
            }else{
                next('/');
            }
        }
    }
    else{
        next();
    }
});

FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  store,
  router,
  render: h => h(App)
}).$mount('#app-box')



