
import axios from 'axios'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import VChart from 'vux/src/components/v-chart/v-chart.vue'
import VLine from 'vux/src/components/v-chart/v-line.vue'
import VArea from 'vux/src/components/v-chart/v-area.vue'
import VTooltip from 'vux/src/components/v-chart/v-tooltip.vue'
import VScale from 'vux/src/components/v-chart/v-scale.vue'
import VAxis from 'vux/src/components/v-chart/v-axis.vue'
import VPoint from 'vux/src/components/v-chart/v-point.vue'


export default {
name: 'indexM4',
data(){
return {
timeImg:require("../../../static/img/timeImg.png"),
show:false,
startTime:"",
endTime:"",
alertSH:false,
chartSH:false,
chartMsg:"",
modelTime:"",
postTime:[],
gradient: [
[0, '#A63EF0'],
[1, '#A63EF0']
],
data: [
{ time: "2019-03-28", tem:0 },
]
}
},
components:{
Popup,
Group,
Calendar,
Datetime,
XButton,
Alert,
VChart,
VLine,
VArea,
VTooltip,
VScale,
VAxis ,
VPoint
},
created(){
this.chartData()
},
methods:{

popupClick(){
this.show = true
},

determine(){
if(this.startTime==""||this.endTime==""){
this.alertSH = true
}else{
let time = [this.startTime,this.endTime];
this.modelTime = this.startTime+"~"+this.endTime;
this.postTime =[this.startTime,this.endTime];
console.log(time)
this.show = false;
this.chartData()
}
},

popupClose(){
this.show = false
},

chartData(){
let dateTime = {

"service_id":localStorage.getItem("userId"),
"time" : this.postTime
}
axios.post('/v1/server_achievement',this.$qs.stringify(dateTime))
.then((response)=>{
let startTimes,endTimes;
for( let i=0;i<response.data.data.length;i++){
startTimes = response.data.data[0].time;
endTimes = response.data.data[response.data.data.length-1].time;
}
this.modelTime = startTimes+"~"+endTimes;
let meter = response.data.data
let obj = meter.map(function (item) {
return{
"tem":item.achievement,
"time":item.time
}
})
this.data = obj
if(obj.length==0){
this.chartSH = true
this.chartMsg = "开始时间和结束时间选错或选择的时间段没有数据"
}

})
.catch((error)=>{
this.chartSH = true
this.chartMsg = error.response.data.message
console.log(error.response.data.message)
})
},
beforeDestroy() {
this.data = []
}
}
}



