
import axios from 'axios'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Loading from 'vux/src/components/loading/index.vue'

export default {
name: 'indexM3',
data(){
return {
loadMain:"玩命加载中...",
LoadingSH:false,
reserve_today_user_count:"",
reserve_today_count:"",
apply_today_count:"",
apply_today_money:"",
apply_month_count:"",
apply_month_money:"",
turnover_rate:"",
month_real_income:"",
users_count:"",
users_money:"",
rankName:"",
rankMoney:""
}
},
components:{
Grid,
GridItem,
Loading
},
created(){
axios.post('/v1/userinfo')
.then((response)=>{
this.headimgurl = response.data.data.users.headimgurl;
this.realname = response.data.data.service.realname;
localStorage.setItem("userId",response.data.data.users.user_id);
this.getList()
})
.catch((error)=>{
})

},
methods:{
getList(){
this.LoadingSH=true;
let data={

"service_id":localStorage.getItem("userId")
}
axios.post('/v1/server_index',this.$qs.stringify(data))
.then((response)=>{
console.log(response.data.data)
let resData = response.data.data;
this.reserve_today_user_count = resData.reserve_today_user_count;
this.reserve_today_count = resData.reserve_today_count;
this.apply_today_count = resData.apply_today_count;
this.apply_today_money = resData.apply_today_money;
this.apply_month_count = resData.apply_month_count;
this.apply_month_money = resData.apply_month_money;
this.turnover_rate = resData.turnover_rate;
this.month_real_income = resData.month_real_income;
this.users_count = resData.users_count;
this.users_money = resData.users_money;
this.rankName = resData.rank_top.name;
this.rankMoney = resData.rank_top.money;
this.LoadingSH = false;

})
.catch((error)=>{

})
}
},
beforeDestroy() {
this.reserve_today_user_count = '';
this.reserve_today_count = '';
this.apply_today_count = '';
this.apply_today_money = '';
this.apply_month_count = '';
this.apply_month_money = '';
this.turnover_rate = '';
this.month_real_income = '';
this.users_count = '';
this.users_money = '';
this.rankName = '';
this.rankMoney = '';
},
}



