
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import { setTimeout } from 'timers';

export default {
name:"teamM1",
data(){
return {
chartSH:false,
waterStartTitle:"请选择开始时间",
waterEndTitle:"请选择结束时间",
waterStartTime:"",
waterEndTime:"",
wHospVal:"",
wServerVal:"",
wUserVal:"",
}
},
components:{
Group,
Datetime,
XInput,
PopupPicker,
Alert
},
methods:{

timeChange1(value){
this.waterStartTime = value;
if(value){
this.waterStartTitle="";
}
},
timeChange2(value){
this.waterEndTime = value;
if(value){
this.waterEndTitle="";
}
},

resetBtn(){
this.waterStartTime="";
this.waterStartTitle="请选择开始时间";
this.waterEndTime="";
this.waterEndTitle="请选择结束时间";
this.wHospVal="";
this.wServerVal="";
this.wUserVal="";
localStorage.removeItem("waterStartTime");
localStorage.removeItem("waterEndTime");
localStorage.removeItem("wHospVal");
localStorage.removeItem("wServerVal")
localStorage.removeItem("wUserVal")
},

queryBtn(){





if(this.waterStartTime==""&&this.waterEndTime==""&&this.wHospVal==""&&this.wServerVal==[]&&this.wUserVal==""){
this.chartSH = true;
}else{
localStorage.setItem("waterStartTime",this.waterStartTime);
localStorage.setItem("waterEndTime",this.waterEndTime);
localStorage.setItem("wHospVal",this.wHospVal);
localStorage.setItem("wServerVal",this.wServerVal)
localStorage.setItem("wUserVal",this.wUserVal)
this.$router.push({path:'runwater'});
}
}
}
}



