
import axios from 'axios'
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
data () {
return {
loadMain:"玩命登录中...",
msg: 'Hello World!',
logoSrc:require("../../../static/img/logo.png"),
userIcon:require("../../../static/img/userIcon.png"),
userClose:require("../../../static/img/userClose.png"),
pwdImg:require("../../../static/img/pwd.png"),
hideImg:require("../../../static/img/hideImg.png"),
pSH:false,
promptTxt:"",
flag:0,
LoadingSH:false,
}
},
components: {
XButton,
Loading
},
methods:{

clearUser(){
this.$refs.input1.value="";
},

viewPwd(){
if(this.flag%2 == 0){
this.$refs.input2.type = "text";
this.hideImg = require("../../../static/img/showImg.png")
}else{
this.$refs.input2.type = "password";
this.hideImg = require("../../../static/img/hideImg.png")
}
this.flag++;
},

submitBtn(){
this.LoadingSH = true;
var test = {
"userName":this.$refs.input1.value,
"password":this.$refs.input2.value,
}
axios.post('/v1/login',this.$qs.stringify(test))
.then((response)=>{



localStorage.setItem("token",response.data.data.access_token);
localStorage.setItem("tokent",response.data.data.token_type);
this.$router.push({path:'index'});
this.LoadingSH = false;
})
.catch((error)=>{
this.LoadingSH = false;
this.pSH = true;
this.promptTxt=error.response.data.message

})
},
},
}



