
import axios from 'axios'
import Loading from 'vux/src/components/loading/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'



const pulldownDefaultConfig = {
content: '下拉刷新',
height: 40,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '释放后刷新',
loadingContent: '正在刷新...',
clsPrefix: 'xs-plugin-pulldown-'
}


export default {
name:"team",
data(){
return {
loadMain:"玩命加载中...",
heightS:"200px",
pSH:false,
LoadingSH:true,
nickname:"服务商",
role_id:"级别",
membersNum:"客户数",
serviceAllReward:"总业绩",
resData:[],
flag:0,
isA: true, //当isA改变时，将更新class
isB: false, //当isB改变时，将更新class
pulldownDefaultConfig: pulldownDefaultConfig,

onFetching: false,
listdata: [],
downdata: [], // 上拉更多的数据存放数组
bottomCount: 30,
counter: 1, //当前页
num: 10, // 一页显示多少条
pageStart: 0, // 开始页数
pageEnd: 0, // 结束页数
}
},
components:{
Loading,
Scroller,
LoadMore,
InlineLoading
},
mounted: function() {
},
created(){
this.getList(1,16);
let heightS = document.documentElement.clientHeight-50+"px";
this.heightS=heightS
},
methods:{

refresh() {
this.downdata=[];
this.pSH = false;
var that = this;
setTimeout(function(){
that.getList(1,16);
that.$refs.scrollerBottom.reset({top: 0})
},2000)
},

getList(page,limit){
let data={

"add_time":[localStorage.getItem("teamStartTime"),localStorage.getItem("teamEndTime")],
"keyword":localStorage.getItem("teamPhoneVal"),
"service_level":localStorage.getItem("service_level"),
"page":page,
"limit":limit,
"server_id":localStorage.getItem("userId")
}
axios.post('/v1/group',this.$qs.stringify(data))
.then((response)=>{
let resData = response.data.data.data;
this.resData = resData;
this.LoadingSH = false;
if(resData.length==0){
this.pSH = true;
}
})
.catch((error)=>{
this.pSH = true;
this.LoadingSH = false;


})
},

onScrollBottom () {
if (this.onFetching) {

} else {

this.onFetching = true
this.counter++;
let end = this.pageEnd = this.num * this.counter;
let i = this.pageStart = this.pageEnd - this.num;
let data={

"add_time":[localStorage.getItem("teamStartTime"),localStorage.getItem("teamEndTime")],
"keyword":localStorage.getItem("teamPhoneVal"),
"service_level":localStorage.getItem("service_level"),
"page":this.counter,
"limit":15,
"server_id":localStorage.getItem("userId")
}
axios.post('/v1/group',this.$qs.stringify(data))
.then((response)=>{

let nickname,role_id,membersNum,serviceAllReward;
for ( let i = 1; i < response.data.data.data.slice(0,20).length; i++) {
let resi=response.data.data.data.slice(0,20)[i]
nickname = resi.realname
role_id = resi.role_id
membersNum = resi.membersNum
serviceAllReward= resi.serviceAllReward
this.downdata.push({
nickname:nickname,
role_id:role_id,
membersNum:membersNum,
serviceAllReward:serviceAllReward
})
this.onFetching = false
}
if(response.data.data.data.length==0){





this.onFetching = false;

}
})
.catch((error)=>{
this.LoadingSH = false;

})

}
}
}
}



