
import manageRouter from '@/components/manageRouter'
import axios from 'axios'
import Loading from 'vux/src/components/loading/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


const pulldownDefaultConfig = {
content: '下拉刷新',
height: 40,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '释放后刷新',
loadingContent: '正在刷新...',
clsPrefix: 'xs-plugin-pulldown-'
}

export default {
data(){
return{
userClose:require("../../../static/img/userClose.png"),
order_snS:"",//弹窗预约单号
createds_atS:"",//弹窗预约时间
user_nameS:"",//弹窗客户名字
user_phoneS:"",//弹窗客户手机
projectsS:"",//弹窗项目名称
hospital_nameS:"",//弹窗医院
operation_timeS:"",//弹窗预约价
moneyS:"",//弹窗预约类型
areS:"",//弹窗预约状态
areaS:"",//弹窗关闭原因
service_nameS:"",//弹窗服务商
service_phoneS:"",//弹窗服务商手机
all_priceS:"",//弹窗预约备注
consume_moneyS:"",
surplusS:"",
apply:"",
pTxt:"",
p1:true,
p2:true,
p3:true,
user_name:"用户",
projects:"项目",
hospital_name:"医院",
all_price:"金额",
order_time:"创建时间",
isA: true, //当isA改变时，将更新class
isB: false, //当isB改变时，将更新class
isa: true, //当isA改变时，将更新class
isb: false, //当isB改变时，将更新class
loadMain:"玩命加载中...",
heightS:"200px",
pSH:false,
LoadingSH:true,
LoadingSH1:false,
resData:[],
showToast: false,
flag1:1,
flag2:2,
pulldownDefaultConfig: pulldownDefaultConfig,
onFetching: false,
listdata: [],
downdata: [], // 上拉更多的数据存放数组
bottomCount: 30,
counter: 1, //当前页
num: 10, // 一页显示多少条
pageStart: 0, // 开始页数
pageEnd: 0, // 结束页数
position: 'default',
showPositionValue: false,
toastTxt:"",
sortMoney:"desc",
sortTime:"desc",
sortKey:"",
sortType:"",
}
},
components: {
manageRouter,
Loading,
Scroller,
LoadMore,
InlineLoading,
XDialog,
Toast
},
created(){
this.getList(1,16);
let heightS = document.documentElement.clientHeight-90+"px";
this.heightS=heightS;
},
methods:{
refresh() {
this.flag1=0
this.flag2=0
this.pSH = false;
this.storageRemove();
this.downdata=[];
var that = this;
setTimeout(function(){
that.clientFn("all_price","desc")
that.sortType = "desc"
that.flag2=1
that.isa=true;
that.isb=false;
that.flag1=1;
that.isA=true;
that.isB=false;
that.$refs.scrollerBottom.reset({top: 0})
},2000)
},

openDialog(a,b,c,d,e,f,g,h,i,j,k,l,m,n,o,p){
this.order_snS = a
this.createds_atS = b
this.user_nameS = c
this.user_phoneS = d

this.operation_timeS = f
this.moneyS = g
this.hospital_nameS = h
this.service_nameS = i
this.service_phoneS = j
this.areS = k
this.areaS = l
this.all_priceS = m
this.consume_moneyS = n
this.surplusS = o
this.apply = p
this.projectsS = this.apply.length

this.showToast = true
},

closeDialog(){
this.showToast = false
},

getList(page,limit){
let data={

"created_at":[localStorage.getItem("waterStartTime"),localStorage.getItem("waterEndTime")],
"hospital_name":localStorage.getItem("wHospVal"),

"service_keyword":localStorage.getItem("wServerVal"),
"user_keyword":localStorage.getItem("wUserVal"),
"page":page,
"limit":limit,
"sortKey":"all_price",
"sort":"desc",
"server_id":localStorage.getItem("userId")
}
axios.post('/v1/flow_sheet',this.$qs.stringify(data))
.then((response)=>{

let resData = response.data.data.data;
this.resData = resData;
this.LoadingSH = false;
if(resData.length==0){
this.pSH = true;
}
})
.catch((error)=>{
this.pSH = true;
this.LoadingSH = false;
console.log(error.response.data.message)

})
},

onScrollBottom () {
if (this.onFetching) {

} else {

this.onFetching = true
this.counter++;


let data={

"created_at":[localStorage.getItem("waterStartTime"),localStorage.getItem("waterEndTime")],
"hospital_name":localStorage.getItem("wHospVal"),

"service_keyword":localStorage.getItem("wServerVal"),
"user_keyword":localStorage.getItem("wUserVal"),
"page":this.counter,
"limit":15,
"sortKey":this.sortKey,
"sort":this.sortType,
"server_id":localStorage.getItem("userId")
}
console.log(data)
axios.post('/v1/flow_sheet',this.$qs.stringify(data))
.then((response)=>{
console.log("------------")
console.log(response)
let order_sn,
createds_at,
user_name,
user_phone,
projects,
operation_time,
money,
hospital_name,
service_name,
service_phone,
are,
area,
all_price,
consume_money,
surplus,
apply
for ( let i = 1; i < response.data.data.data.slice(0,20).length; i++) {
let resi=response.data.data.data.slice(0,20)[i]
user_name = resi.user_name
projects = resi.projects
hospital_name = resi.hospital_name
createds_at= resi.createds_at
all_price=resi.all_price
order_sn = resi.order_sn
user_phone = resi.user_phone
operation_time = resi.operation_time
money = resi.money
are = resi.are
area = resi.area
service_name = resi.service_name
service_phone = resi.service_phone
consume_money = resi.consume_money
surplus = resi.surplus
apply = resi.apply
this.downdata.push({
user_name:user_name,
projects:projects,
hospital_name:hospital_name,
createds_at:createds_at,
order_sn:order_sn,
user_phone:user_phone,
operation_time:operation_time,
money:money,
are:are,
area:area,
service_name:service_name,
service_phone:service_phone,
all_price:all_price,
consume_money:consume_money,
surplus:surplus,
apply:apply
})
this.onFetching = false
}
if(response.data.data.data.length==0){
this.onFetching = false;
}

})
.catch((error)=>{
this.LoadingSH = false;
this.onFetching = false;
this.pTxt = "服务器繁忙";
this.showPositionValue = true;
this.toastTxt = "服务器繁忙"
})

}
},
clientFn(types,s){
let data={

"created_at":[localStorage.getItem("waterStartTime"),localStorage.getItem("waterEndTime")],
"hospital_name":localStorage.getItem("wHospVal"),
"service_keyword":localStorage.getItem("wServerVal"),
"user_keyword":localStorage.getItem("wUserVal"),
"page":this.counter,
"limit":15,
"sortKey":types,
"sort":s,
"server_id":localStorage.getItem("userId")
}
axios.post('/v1/flow_sheet',this.$qs.stringify(data))
.then((response)=>{
let resData = response.data.data.data;
console.log(response)
if(resData.length==0){

}else{
this.resData = resData;
this.LoadingSH = false;
}
})
.catch((error)=>{
this.LoadingSH = false;
this.pSH = true
this.onFetching = false;
this.pTxt = "服务器繁忙";
this.showPositionValue = true;
this.toastTxt = "服务器繁忙"
})
},

clientSort1(){
this.sortKey = "all_price"
this.LoadingSH = true
this.downdata=[]
if(this.flag1==0){
this.clientFn("all_price","desc")
this.sortType = "desc"
this.flag1=1;
this.isA=true;
this.isB=false;
}else{
this.clientFn("all_price","asc")
this.sortType = "asc"
this.flag1=0
this.isA=false;
this.isB=true;
}
document.getElementsByClassName("xs-container")[0].style.transform = 'translateX(0px) translateY(0px) translateZ(0px) scale(1, 1)'
},

clientSort2(){
this.pSH = false
this.sortKey = "created_at"
this.LoadingSH = true
this.downdata=[]
if(this.flag2==0){
this.clientFn("created_at","desc")
this.sortType = "desc"
this.flag2=1;
this.isa=true;
this.isb=false;
}else{
this.clientFn("created_at","asc")
this.sortType = "asc"
this.downdata=[]
this.flag2=0
this.isa=false;
this.isb=true;
}
document.getElementsByClassName("xs-container")[0].style.transform = 'translateX(0px) translateY(0px) translateZ(0px) scale(1, 1)'
},

storageRemove(){
localStorage.removeItem("waterStartTime");
localStorage.removeItem("waterEndTime");
localStorage.removeItem("wHospVal");
localStorage.removeItem("wServerVal")
localStorage.removeItem("wUserVal")
}
},
beforeDestroy(){
this.storageRemove()
}
}



