import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
let store = new Vuex.Store({
    // 1. state
    state:{
        tokenId:"",
        tokenType:""
    },
    // 2. getters
    getters:{
        tokenIdFn(state){
            return state.tokenId
        },
        tokenTypeFn(state){
            return state.tokenType
        }
    },
    // 3. actions
    actions:{
        setTokenName({commit,state}, name){
            commit("setTokenId", name);
        },
        setTokenTName({commit,state}, name){
            commit("setTokenType", name);
        }
    },
    // 4. mutations
    mutations:{
        setTokenId(state, name){
            state.tokenId = name;
        },
        setTokenType(state, name){
            state.tokenType = name;
        }
    }
});

export default store;


