
import axios from 'axios'
import Confirm from 'vux/src/components/confirm/index.vue'


export default {
name: 'indexM1',
components: {
Confirm
},
data(){
return {
show:false,
headimgurl:"",
realname:""
}
},
created(){
axios.post('/v1/userinfo')
.then((response)=>{
this.headimgurl = response.data.data.users.headimgurl;
this.realname = response.data.data.service.realname;
localStorage.setItem("userId",response.data.data.users.user_id);

})
.catch((error)=>{

})
},
methods:{

logOut(){
this.show = true
},

onCancel () {

},

onConfirm (msg) {

console.log(localStorage.removeItem("token"))
console.log(localStorage.removeItem("tokent"))
this.$router.push({path:'/'});

},
}
}



