import Vue from 'vue'
import Router from 'vue-router'
import login from '@/page/login/login'
import index from '@/page/index/index'
import team from '@/page/team/team'
import teamM1 from '@/page/team/teamM1'
import make from '@/page/make/make'
import makeM1 from '@/page/make/makeM1'
import runwater from '@/page/runwater/runwater'
import runwaterM1 from '@/page/runwater/runwaterM1'
import subsidies from '@/page/subsidies/subsidies'
import subsidiesM1 from '@/page/subsidies/subsidiesM1'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login,
      meta:{title:"服务商业绩系统"}
    },
    {
      path: '/index',
      name: 'index',
      component: index,
      meta:{title:"主页"}
    },
    {
      path: '/team',
      name: 'team',
      component: team,
      meta:{title:"团队管理"}
    },
    {
      path: '/teamM1',
      name: 'teamM1',
      component: teamM1,
      meta:{title:"团队筛选"}
    },
    {
      path: '/make',
      name: 'make',
      component: make,
      meta:{title:"预约管理"}
    },
    {
      path: '/makeM1',
      name: 'makeM1',
      component: makeM1,
      meta:{title:"预约筛选"}
    },
    {
      path: '/runwater',
      name: 'runwater',
      component: runwater,
      meta:{title:"流水管理"}
    },
    {
      path: '/runwaterM1',
      name: 'runwaterM1',
      component: runwaterM1,
      meta:{title:"流水筛选"}
    },
    {
      path: '/subsidies',
      name: 'subsidies',
      component: subsidies,
      meta:{title:"补贴管理"}
    },
    {
      path: '/subsidiesM1',
      name: 'subsidiesM1',
      component: subsidiesM1,
      meta:{title:"补贴筛选"}
    }
  ]
})



