
import manageRouter from '@/components/manageRouter'
import axios from 'axios'
import Loading from 'vux/src/components/loading/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { setTimeout } from 'timers';

const pulldownDefaultConfig = {
content: '下拉刷新',
height: 40,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '释放后刷新',
loadingContent: '正在刷新...',
clsPrefix: 'xs-plugin-pulldown-'
}


export default {
data(){
return{
userClose:require("../../../static/img/userClose.png"),
order_snS:"",//弹窗预约单号
order_timeS:"",//弹窗预约时间
realnameS:"",//弹窗客户名字
usernameS:"",//弹窗客户手机
project_nameS:"",//弹窗项目名称
hospital_nameS:"",//弹窗医院
project_priceS:"",//弹窗预约价
order_typeS:"",//弹窗预约类型
order_statusS:"",//弹窗预约状态
consultS:"",//弹窗关闭原因
server_nameS:"",//弹窗服务商
server_phoneS:"",//弹窗服务商手机
submit_remarkS:"",//弹窗预约备注


realname:"用户",
project_name:"项目",
hospital_name:"医院",
order_time:"预约时间",
isA: true, //当isA改变时，将更新class
isB: false, //当isB改变时，将更新class
loadMain:"玩命加载中...",
heightS:"200px",
pSH:false,
LoadingSH:true,
LoadingSH1:false,
resData:[],
showToast: false,
flag:1,
pulldownDefaultConfig: pulldownDefaultConfig,
onFetching: false,
listdata: [],
downdata: [], // 上拉更多的数据存放数组
bottomCount: 30,
counter: 1, //当前页
num: 10, // 一页显示多少条
pageStart: 0, // 开始页数
pageEnd: 0, // 结束页数
position: 'default',
showPositionValue: false,
toastTxt:"",
sortTime:"desc",
}
},
components: {
manageRouter,
Loading,
Scroller,
LoadMore,
InlineLoading,
XDialog,
Toast
},
created(){
this.getList(1,16);
let heightS = document.documentElement.clientHeight-90+"px";
this.heightS=heightS
},
methods:{

refresh() {
this.flag=0
this.pSH = false;
this.storageRemove();
this.downdata=[];
var that = this;
setTimeout(function(){
that.clientFn("desc")
that.sortDa = "desc"
that.flag=1
that.isA=true;
that.isB=false;
that.$refs.scrollerBottom.reset({top: 0})
},2000)
},

openDialog(a,b,c,d,e,f,g,h,i,j,k,l,m){
this.order_snS = a
this.order_timeS = b
this.realnameS = c
this.usernameS = d
this.project_nameS = e
this.hospital_nameS = f
this.project_priceS = g
this.order_typeS = h
this.order_statusS = i
this.consultS = j
this.server_nameS = k
this.server_phoneS = l
this.submit_remarkS = m
this.showToast = true
},

closeDialog(){
this.showToast = false
},

getList(page,limit){
let makeCateId,data;
if(localStorage.getItem("makeCateId")==null||localStorage.getItem("makeCateId")==""){
data={

"order_time":[localStorage.getItem("makeStartTime"),localStorage.getItem("makeEndTime")],
"hospital_name":localStorage.getItem("hospVal"),
"server_name":localStorage.getItem("serverVal"),
"username":localStorage.getItem("userVal"),
"page":page,
"limit":limit,
"sortKey":"order_time",
"sort":"desc",
"server_id":localStorage.getItem("userId")
}
}else{
data={

"order_time":[localStorage.getItem("makeStartTime"),localStorage.getItem("makeEndTime")],
"hospital_name":localStorage.getItem("hospVal"),
"server_name":localStorage.getItem("serverVal"),
"username":localStorage.getItem("userVal"),
"cate_id":[localStorage.getItem("makeCateId")],
"page":page,
"limit":limit,
"sortKey":"order_time",
"sort":"desc",
"server_id":localStorage.getItem("userId")
}
}
axios.post('/v1/reserve_sheet',this.$qs.stringify(data))
.then((response)=>{

let resData = response.data.reserves.data;
this.resData = resData;
this.LoadingSH = false;
if(resData.length==0){
this.pSH = true;
}
})
.catch((error)=>{
this.pSH = true;
this.LoadingSH = false;
console.log(error.response.data.message)

})
},

onScrollBottom(){
this.pSH = false
if (this.onFetching) {

} else {

this.onFetching = true
this.counter++;


let makeCateId;
if(localStorage.getItem("makeCateId")==null){
makeCateId = []
}else{
makeCateId = [localStorage.getItem("makeCateId")]
}

let data={

"order_time":[localStorage.getItem("makeStartTime"),localStorage.getItem("makeEndTime")],
"hospital_name":localStorage.getItem("hospVal"),
"server_name":localStorage.getItem("serverVal"),
"username":localStorage.getItem("userVal"),
"cate_id":makeCateId,
"page":this.counter,
"limit":15,
"sortKey":"order_time",
"sort":this.sortTime,
"server_id":localStorage.getItem("userId")
}
axios.post('/v1/reserve_sheet',this.$qs.stringify(data))
.then((response)=>{

let realname,
project_name,
hospital_name,
order_time,
order_sn,
username,
project_price,
order_type,
order_status,
consult,
server_name,
server_phone,
submit_remark
for ( let i = 1; i < response.data.reserves.data.slice(0,20).length; i++) {
let resi=response.data.reserves.data.slice(0,20)[i]
realname = resi.realname
project_name = resi.project_name
hospital_name = resi.hospital_name
order_time= resi.order_time
order_sn = resi.order_sn
username = resi.username
project_price = resi.project_price
order_type = resi.order_type
order_status = resi.order_status
consult = resi.consult
server_name = resi.server_name
server_phone = resi.server_phone
submit_remark = resi.submit_remark
this.downdata.push({
realname:realname,
project_name:project_name,
hospital_name:hospital_name,
order_time:order_time,
order_sn:order_sn,
username:username,
project_price:project_price,
order_type:order_type,
order_status:order_status,
consult:consult,
server_name:server_name,
server_phone:server_phone,
submit_remark:submit_remark
})
this.onFetching = false
}
if(response.data.reserves.data.length==0){
this.onFetching = false;
}
})
.catch((error)=>{
this.LoadingSH = false;
this.onFetching = false;
this.pTxt = "服务器繁忙";
this.showPositionValue = true;
this.toastTxt = "服务器繁忙"
})

}
},
clientFn(s){
let makeCateId;
if(localStorage.getItem("makeCateId")==null){
makeCateId = []
}else{
makeCateId = [localStorage.getItem("makeCateId")]
}
let data={

"order_time":[localStorage.getItem("makeStartTime"),localStorage.getItem("makeEndTime")],
"hospital_name":localStorage.getItem("hospVal"),
"server_name":localStorage.getItem("serverVal"),
"username":localStorage.getItem("userVal"),
"cate_id":makeCateId,
"page":this.counter,
"limit":15,
"sortKey":"order_time",
"sort":s,
"server_id":localStorage.getItem("userId")
}
axios.post('/v1/reserve_sheet',this.$qs.stringify(data))
.then((response)=>{
let resData = response.data.reserves.data;

if(resData.length==0){

}else{
this.resData = resData;
this.LoadingSH = false;
}
})
.catch((error)=>{
this.LoadingSH = false;
this.pSH = true
this.onFetching = false;
this.pTxt = "服务器繁忙";
this.showPositionValue = true;
this.toastTxt = "服务器繁忙"
})
},
clientSort(){
this.pSH = false
this.LoadingSH = true
this.downdata=[]
if(this.flag==0){
this.clientFn("desc")
this.sortTime = "desc"
this.flag=1;
this.isA=true;
this.isB=false;
}else{
this.clientFn("asc")
this.sortTime = "asc"
this.flag=0
this.isA=false;
this.isB=true;
}
document.getElementsByClassName("xs-container")[0].style.transform = 'translateX(0px) translateY(0px) translateZ(0px) scale(1, 1)'
},

storageRemove(){
localStorage.removeItem("makeStartTime");
localStorage.removeItem("makeEndTime");
localStorage.removeItem("hospVal");
localStorage.removeItem("serverVal")
localStorage.removeItem("userVal")
localStorage.removeItem("makeCateId")
}
},
beforeDestroy(){
this.storageRemove()
}
}



