if(localStorage.getItem("subCateId")==null){
subCateId = []
}else{
subCateId = [localStorage.getItem("subCateId")]
}
let data={

"allowance_time":[localStorage.getItem("subStartTime"),localStorage.getItem("subEndTime")],
"hospital_name":localStorage.getItem("sHospVal"),
"server_name":localStorage.getItem("sServerVal"),
"userphone":localStorage.getItem("sUserVal"),
"cate_id":subCateId,
"page":1,
"limit":15,
"sortKey":"money",
"sort":s,
"server_id":localStorage.getItem("userId")
}
axios.post('/v1/allowance_sheet',this.$qs.stringify(data))
.then((response)=>{
let resData = response.data.allowances.data;
console.log(response)
if(resData.length==0){
this.pSH = true;
}else{
this.resData = resData;
this.LoadingSH = false;
}
})
.catch((error)=>{
this.LoadingSH = false;
this.pSH = true
this.onFetching = false;
this.pTxt = "服务器繁忙";
this.showPositionValue = true;
this.toastTxt = "服务器繁忙"
})
},

clientSort(){
this.LoadingSH = true
this.downdata=[]
if(this.flag==0){
this.clientFn("desc")
this.sortDa = "desc"
this.flag=1;
this.isA=true;
this.isB=false;
}else{
this.clientFn("asc")
this.sortDa = "asc"
this.flag=0
this.isA=false;
this.isB=true;
}
document.getElementsByClassName("xs-container")[0].style.transform = 'translateX(0px) translateY(0px) translateZ(0px) scale(1, 1)'
},

storageRemove(){
localStorage.removeItem("subStartTime");
localStorage.removeItem("subEndTime");
localStorage.removeItem("sHospVal");
localStorage.removeItem("sServerVal")
localStorage.removeItem("sUserVal")
localStorage.removeItem("subCateId")
}
},
beforeDestroy(){
this.storageRemove()
}
}



